.tutor {
  @media (min-width: $desktop-width) {
    display: flex;
  }

  &__image {
    @media (min-width: $desktop-width) {
      flex-shrink: 0;
      width: 300px;
      max-height: 300px;
      margin: 0 20px 0 0;
    }
  }

  &__description {
    @media (min-width: $desktop-width) {
      overflow-y: visible;
      margin: 0;
      border: none;
      border-radius: 0;
    }
  }

  &__caption {
    font-size: 22px;

    @media (min-width: $tablet-width) {
      font-size: 24px;
    }

    @media (min-width: $desktop-width) {
      font-size: 28px;
    }
  }

  &__text {
    margin: 0 0 10px;
    font-size: 16px;

    @media (min-width: $tablet-width) {
      margin: 0 0 20px;
      font-size: 18px;
    }
  }

  &__button {
    position: relative;
    bottom: 0;
    min-width: auto;
    max-width: 280px;
    font-size: 18px;

    @media (min-width: $tablet-width) {
      max-width: 400px;
      margin: 0 auto;
      font-size: 24px;
    }
  }
}
