.social-list {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin: 0 auto 20px;

  & .list__item {
    background-color: transparent;
    background-image: none;
    margin: 0;
    padding: 0;
  }

  &__icon {
    width: 20px;
    height: 20px;
    color: $text-color;

    @media (min-width: $tablet-width) {
      width: 30px;
      height: 30px;
    }
  }
}
