.contacts {
  position: relative;
  padding: 20px 10px 0;

  @media (min-width: $tablet-width) {
    padding: 20px 0 0;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0 20px;
  }

  &__caption {
    text-align: left;
  }

  &__wrap {
    margin: 0 0 10px;

    &:last-of-type {
      margin: 0;
    }
  }

  &__address {
    margin: 0 0 10px;
    font-size: 14px;
    line-height: 1.4;

    @media (min-width: $tablet-width) {
      font-size: 18px;
    }
  }

  &__map {
    position: relative;
    overflow: hidden;

    @media (min-width: $desktop-width) {
      min-width: 750px;
    }

    & iframe {
      width: 100%;
      height: 200px;

      @media (min-width: $tablet-width) {
        height: 270px;
      }
    }
  }

  &__list {
    margin: 0 0 20px;

    @media (min-width: $desktop-width) {
      flex-direction: column;
      align-items: flex-start;
    }

    & .list__item {
      margin: 0 0 8px;
      background-color: transparent;
      background-image: none;
      color: $text-color;
      font-size: 12px;
      transition: opacity 0.2s linear;

      &:hover,
      &:focus {
        opacity: 0.8;
        transform: none;
      }

      &--accented {
        color: $main-color;
      }

      &:last-child {
        margin: 0;
      }

      @media (min-width: $tablet-width) {
        flex-basis: 30%;
        margin: 0;
      }

      @media (min-width: $desktop-width) {
        margin: 0 0 10px;
        font-size: 16px;
        text-align: left;
      }
    }

    & .list__item-link {
      padding: 0;
    }

    &--small {
      justify-content: center;
      align-items: center;
      width: 100%;
      margin: 0;

      & .list__item {
        margin: 0 5px 5px 0;
        font-size: 10px;

        @media (min-width: $tablet-width) {
          font-size: 12px;
        }
      }
    }
  }

  &__button {
    margin: 0 0 25px;
    padding: 10px;
    font-size: 18px;

    @media (min-width: $tablet-width) {
      min-width: auto;
      width: 300px;
      margin: 0 auto 25px;
    }

    @media (min-width: $desktop-width) {
      padding: 15px;
      font-size: 20px;
    }
  }
}
