.footer {
  align-self: end;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  background-color: $main-color;

  @media (min-width: $tablet-width) {
    padding: 8px 5%;
  }

  &__text {
    margin: 0;
    font-size: 10px;

    @media (min-width: $tablet-width) {
      font-size: 14px;
    }
  }

  &__link {
    text-decoration: underline;
    text-decoration-color: currentColor;
  }

  &__list {
    @media (min-width: $tablet-width) {
      flex-direction: row;
    }

    .list__item {
      margin: 0 0 5px;
      padding: 2px;
      background-color: transparent;
      color: $text-color;
      font-size: 10px;
      font-style: italic;

      &:last-child {
        margin: 0;
      }

      &-link {
        padding: 0;
      }

      @media (min-width: $tablet-width) {
        margin: 0 20px 0 0;
        font-size: 16px;
      }

      @media (min-width: $desktop-width) {
        flex-basis: auto;
      }
    }
  }
}
