.cabinet {
  padding: 10px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 10px 0;
  }

  &__wrap {
    overflow: hidden;
    margin: 0 0 25px;

    @media (min-width: $tablet-width) {
      width: 350px;
    }

    @media (min-width: $desktop-width) {
      width: 700px;
    }

    &:first-of-type {
      @media (min-width: $tablet-width) {
        position: sticky;
        top: 72px;
        max-width: 300px;
      }

      @media (min-width: $desktop-width) {
        max-width: 450px;
      }
    }
  }

  &__photo {
    display: block;
    margin: 0 auto 5px;
  }

  &__caption {
    display: block;
  }

  &__button {
    min-width: auto;
    max-width: 200px;
    margin: 0 auto 20px;
    padding: 5px;
    box-shadow: none;
    background-color: $second-accent-color;
    color: $text-color;
    font-size: 14px;
    font-weight: 400;

    &:hover,
    &:focus {
      &::after {
        background-color: $second-accent-color;
      }
    }

    &--wide {
      width: 100%;
      max-width: none;
      padding: 10px;
      font-size: 16px;

      @media (min-width: $tablet-width) {
        padding: 15px;
        font-size: 18px;
      }
    }
  }

  &__list {
    @media (min-width: $tablet-width) {
      flex-direction: column;
      align-items: stretch;
    }

    @media (min-width: $desktop-width) {
      & .list__item:hover,
      & .list__item:focus {
        transform: none;
      }
    }
  }
}
