.input {
  display: block;
  width: 100%;
  padding: 12px 10px;
  border: 2px solid $second-accent-color;
  border-radius: 4px;
  box-shadow: none;
  background-color: transparent;
  font-family: inherit;
  font-size: 15px;
  transition: border-color 0.5s ease-in-out;

  @media (min-width: $desktop-width) {
    padding: 16px 10px;
    font-size: 18px;
  }

  &:focus {
    outline: none;
    border-color: $main-color;
  }

  &:not(:placeholder-shown):not(:focus):valid {
    border-color: $confirm-color;
  }

  &:focus + .form__label,
  &:not(:placeholder-shown) + .form__label {
    top: -24px;
    transform: none;
  }

  &--textarea {
    min-height: 100px;
    resize: none;

    @media (min-width: $desktop-width) {
      min-height: 130px;
    }

    & + .form__label {
      top: 10px;
      transform: none;
    }
  }

  &--file {
    margin: 0 0 25px;
    border-color: $second-accent-color;

    &:focus {
      outline: none;
      border-color: $second-accent-color;
    }

    &:not(:placeholder-shown):not(:focus):valid {
      border-color: $second-accent-color;
    }
  }
}
