@font-face {
  font-family: $main-font-family;
  src: url("../fonts/fira-sans-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $main-font-family;
  src: url("../fonts/fira-sans-regular-italic.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: $main-font-family;
  src: url("../fonts/fira-sans-extra-bold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $main-font-family;
  src: url("../fonts/fira-sans-extra-bold-italic.woff2") format("woff2");
  font-weight: 800;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: $caption-font-family;
  src: url("../fonts/kurale-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $monospace-font-family;
  src: url("../fonts/fira-mono-medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
