.main {
  position: relative;
  display: grid;
  margin: 0 0 10px;

  @media (min-width: $tablet-width) {
    width: 700px;
    margin: 0 auto 10px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    width: 1200px;
    margin: 0 auto 30px;
  }

  &__article {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;

      & .article__description {
        border: none;
      }
    }
  }

  &__button {
    align-self: start;
    display: block;
    width: 90vw;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      min-width: auto;
      width: 60vw;
    }

    @media (min-width: $desktop-width) {
      width: 600px;
    }
  }
}
