.js-dropdown-toggle {
  position: relative;
  z-index: 1;
  cursor: pointer;

  &::after {
    position: absolute;
    top: 50%;
    left: auto;
    right: 10px;
    content: "";
    width: 8px;
    height: 8px;
    border-left: 2px solid currentColor;
    border-top: 2px solid currentColor;
    background-color: transparent;
    transform: translateY(-50%) rotate(225deg);
    filter: none;
    transition: transform 0.2s linear;
  }

  &:hover {
    &::after {
      position: absolute;
      top: 50%;
      left: auto;
      right: 10px;
      content: "";
      width: 8px;
      height: 8px;
      border-left: 2px solid currentColor;
      border-top: 2px solid currentColor;
      background-color: transparent;
      transform: translateY(-50%) rotate(225deg);
      filter: none;
      transition: transform 0.2s linear;
    }
  }

  &.js-dropdown-open {
    &::after {
      transform: translateY(-50%) rotate(45deg);
    }

    & + .js-dropdown-content {
      position: static;
      max-height: fit-content;
      visibility: visible;
      transform: translateY(0%);
    }
  }
}

.js-dropdown-content {
  position: absolute;
  overflow: hidden;
  max-height: 0;
  width: 100%;
  visibility: hidden;
  transform: translateY(-5%);
  transition: transform 0.2s linear;
}
