.logo {
  position: absolute;
  top: 50%;
  left: 8px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: $second-accent-color;
  transform: translateY(-50%);

  @media (min-width: $tablet-width) {
    width: 55px;
    height: 55px;
  }

  @media (min-width: $desktop-width) {
    left: 10%;
    width: 30px;
    height: 30px;
  }
}
