.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: $bg-color;

  @media (min-width: $tablet-width) {
    padding: 15px;
  }

  @media (min-width: $desktop-width) {
    padding: 18px;
  }

  &__wrap {
    width: 50%;
    margin: 0;
    text-align: center;

    &:only-child {
      width: 100%;
    }
  }

  &__label {
    display: inline-block;
    margin: 0 0 5px;
    font-style: italic;

    @media (min-width: $tablet-width) {
      font-size: 18px;
    }
  }
}
