:root {
  --bg-color: hsl(255, 100%, 100%);
  --text-color: hsl(33, 85%, 5%);
}

[data-theme="dark"] {
  --bg-color: hsl(33, 85%, 5%);
  --text-color: hsl(255, 100%, 100%);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: grid;
  grid-template-rows: max-content;
  min-height: 100vh;
  margin: 0;
  background-color: $bg-color;
  color: $text-color;
  font-family: $main-font-family;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
}

img {
  max-width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}
