.article {
  display: grid;
  grid-template-rows: max-content auto max-content;
  gap: 14px;
  padding: 8px 8px 14px;

  @media (min-width: $desktop-width) {
    padding: 0;
  }

  &__image {
    width: 100%;

    @media (min-width: $desktop-width) {
      max-height: 300px;
      object-fit: cover;
    }
  }

  &__description {
    overflow-y: auto;
    padding: 10px;
    border: 2px solid $main-color;
    border-radius: 12px;

    @media (min-width: $tablet-width) {
      padding: 20px;
      border-radius: 16px;
    }

    @media (min-width: $desktop-width) {
      margin: 0 0 20px;
      padding: 0;
    }
  }

  &__text {
    font-size: 20px;
    font-style: italic;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 22px;
    }
  }

  &__button {
    position: sticky;
    bottom: 12px;
    padding: 14px;

    @media (min-width: $tablet-width) {
      padding: 24px;
    }

    @media (min-width: $desktop-width) {
      position: relative;
      min-width: 600px;
      margin: 0 auto;
    }
  }
}
