.list {
  @include reset-list;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
  }

  &__item {
    margin: 0 0 15px;
    border-radius: 5px;
    background-color: $confirm-color;
    background-image: radial-gradient(circle 26em at 65% 15%, $confirm-color, darken($confirm-color, 50%));
    color: $bg-color;
    font-size: 16px;
    letter-spacing: 1px;
    text-align: center;

    &:last-child {
      margin: 0;
    }

    &--accented {
      color: $main-color;
    }

    &-link {
      display: block;
      width: 100%;
      height: 100%;
      padding: 10px 0;

      @media (min-width: $tablet-width) {
        padding: 16px 0;
      }

      @media (min-width: $desktop-width) {
        padding: 18px 0;
      }
    }

    &-content {
      padding: 10px;
      border: 2px solid $confirm-color;
      border-top: none;
      border-radius: 0 0 5px 5px;
      background-color: $bg-color;
    }

    &-text {
      margin: 0 0 5px;
      color: $text-color;
    }

    &-button {
      min-width: auto;
      padding: 5px;
      box-shadow: none;
      background-color: $main-color;
      font-size: 14px;

      &:hover,
      &:focus {
        &::after {
          background-color: $main-color;
        }
      }

      @media (min-width: $desktop-width) {
        max-width: 300px;
        margin: 0 auto;
        padding: 10px;
        font-size: 18px;
      }
    }

    @media (min-width: $tablet-width) {
      flex-basis: 45%;
    }

    @media (min-width: $desktop-width) {
      flex-basis: 30%;
      margin: 0 0 18px;
      font-size: 20px;
    }

    @media (min-width: $desktop-width) {
      transition: transform 0.2s linear;

      &:hover,
      &:focus {
        transform: scale(1.02);
      }
    }
  }
}
