.form {
  display: flex;
  flex-direction: column;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__wrap {
    position: relative;
    margin: 0 0 30px;
    z-index: 0;

    @media (min-width: $desktop-width) {
      flex-basis: 30%;
      margin: 0 0 35px;

      &--wide {
        flex-basis: 100%;
      }
    }
  }

  &__label {
    position: absolute;
    top: 50%;
    left: 10px;
    font-size: 15px;
    transition: top 0.5s ease-in-out;
    transform: translateY(-50%);

    @media (min-width: $desktop-width) {
      font-size: 18px;
    }

    &--static {
      position: static;
      display: block;
    }

    & sup {
      color: $decline-color;
      line-height: 1;
    }
  }

  &__button {
    align-self: center;

    @media (min-width: $desktop-width) {
      min-width: auto;
      width: 100%;
      margin: 0 0 20px;
    }
  }
}
