.calendar {
  width: 100%;
  border-collapse: collapse;

  @media (min-width: $desktop-width) {
    width: 70%;
    margin: 0 auto;
  }

  &__caption {
    margin: 0 0 10px;
    font-family: $main-font-family;
    font-size: 18px;

    @media (min-width: $tablet-width) {
      font-size: 28px;
    }
  }

  &__cell {
    padding: 4% 0;
    border: 1px solid $bg-color;
    background-color: $confirm-color;
    background-image: radial-gradient(circle 26em at 65% 15%, $confirm-color, darken($confirm-color, 50%));
    color: $bg-color;
    font-family: $monospace-font-family;
    font-size: 17px;
    font-weight: 500;
    text-align: center;
    transition: transform 0.2s linear;

    &:hover {
      transform: scale(1.02);
    }

    @media (min-width: $tablet-width) {
      font-size: 22px;
    }

    @media (min-width: $desktop-width) {
      padding: 2% 0;
    }

    &--heading {
      background-color: $main-color;
      background-image: none;
      color: $text-color;
      font-weight: 400;

      &:hover {
        transform: none;
      }
    }

    &--empty {
      background-color: fade_out($second-accent-color, 0.6);
      background-image: none;

      &:hover {
        transform: none;
      }
    }

    &--free {
      background-color: fade_out($second-accent-color, 0.6);
      background-image: none;
      color: transparent;

      &:hover {
        transform: none;
      }
    }
  }
}
