.menu {
  @include reset-list;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  box-shadow: 0 3px 10px fade_out($second-accent-color, 0.5);
  background-color: $bg-color;

  &--main {
    box-shadow: none;

    @media (min-width: $desktop-width) {
      box-shadow: 0 3px 10px fade_out($second-accent-color, 0.8);
    }

    & .menu__item {
      &-link {
        padding: 14px 0;

        @media (min-width: $tablet-width) {
          padding: 22px 0;
        }

        @media (min-width: $desktop-width) {
          padding: 8px 0;
        }
      }

      @media (min-width: $tablet-width) {
        font-size: 22px;
      }

      @media (min-width: $desktop-width) {
        font-size: 16px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    box-shadow: 0 3px 10px fade_out($second-accent-color, 0.8);
    background-color: fade_out($main-color, 0.5);
  }

  &__item {
    border-bottom: 1px solid $second-accent-color;
    font-size: 18px;
    letter-spacing: 1px;
    text-align: center;

    @media (min-width: $tablet-width) {
      border-width: 2px;
      font-size: 22px;
    }

    @media (min-width: $desktop-width) {
      flex-basis: 20%;
      flex-grow: 1;
      border-right: 1px solid $bg-color;
      border-bottom: none;
      font-size: 16px;
    }

    &:last-child {
      border: none;
    }

    &-link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      padding: 20px 0;

      &:not([href]) {
        color: $main-color;
        font-style: italic;

        @media (min-width: $desktop-width) {
          background-color: $main-color;
          color: $text-color;
        }
      }

      @media (min-width: $tablet-width) {
        padding: 28px 0;
      }

      @media (min-width: $desktop-width) {
        padding: 8px 0;
        transition: background-color 0.2s linear;

        &:hover,
        &:focus {
          background-color: $main-color;
        }
      }
    }
  }
}
