.caption {
  margin: 0;
  font-family: $caption-font-family;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.1;

  @media (min-width: $tablet-width) {
    font-size: 24px;
  }

  &--small {
    margin: 0 0 15px;
    font-size: 18px;
    text-align: center;

    @media (min-width: $tablet-width) {
      margin: 0 0 20px;
      font-size: 24px;
    }

    @media (min-width: $desktop-width) {
      font-size: 28px;
    }
  }
}
