.tutors {
  &__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px;

    @media (min-width: $tablet-width) {
      padding: 15px;
    }

    @media (min-width: $desktop-width) {
      justify-content: space-around;
      padding: 30px;
    }

    &-item {
      flex-basis: 47.5%;
      margin: 0 0 14px;
      padding: 1%;
      box-shadow: 4px 4px 10px 0 $shadow-color;
      background-color: fade_out($main-color, 0.5);
      text-align: center;
      transition: transform 0.2s linear;

      &:hover,
      &:focus {
        transform: scale(1.02) translateY(-5px);
      }

      &:active {
        box-shadow: none;
      }

      @media (min-width: $tablet-width) {
        flex-basis: 20%;
        margin: 0 0 30px;
        padding: 2%;
      }

      @media (min-width: $desktop-width) {
        flex-basis: 21%;
      }
    }
  }

  &__link {
    display: block;
    color: inherit;
    text-decoration: none;
  }

  &__image {
    display: inline-block;
    margin: 0 0 7px;

    @media (min-width: $tablet-width) {
      margin: 0 0 10px;
    }

    @media (min-width: $desktop-width) {
      width: 75%;
      margin: 0 0 15px;
    }
  }

  &__name {
    font-size: 14px;

    @media (min-width: $tablet-width) {
      font-size: 22px;
    }
  }
}
