.header {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: $main-color;

  @media (min-width: $desktop-width) {
    position: static;
    margin: 0 0 20px;
    background-color: transparent;
  }

  &--main {
    position: static;
  }

  &__wrap {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 12%;
    box-shadow: 0 3px 10px fade_out($main-color, 0.5);

    @media (min-width: $tablet-width) {
      padding: 16px 5%;
    }

    @media (min-width: $desktop-width) {
      justify-content: center;
      padding: 5px 10%;
      box-shadow: none;
      background-color: $main-color;
    }
  }

  &__caption {
    text-align: center;
  }

  &__image {
    display: block;
    width: 100%;

    @media (min-width: $tablet-width) {
      max-height: 400px;
      object-fit: cover;
    }

    @media (min-width: $desktop-width) {
      max-height: 350px;
    }
  }

  &__navigation {
    position: absolute;
    top: 100%;
    width: 100%;

    @media (min-width: $desktop-width) {
      position: static;
    }
  }

  &.js-menu-closed .navigation {
    opacity: 0;
    transform: translateX(-100%) scale(0.5);

    @media (min-width: $desktop-width) {
      opacity: 1;
      transform: none;
    }
  }

  &:not(.js-menu-closed) .toggle-button svg {
    opacity: 0;
  }

  &:not(.js-menu-closed) .toggle-button::before,
  &:not(.js-menu-closed) .toggle-button::after {
    top: 50%;
    left: 50%;
  }

  &:not(.js-menu-closed) .toggle-button::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &:not(.js-menu-closed) .toggle-button::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}
