.button {
  position: relative;
  display: block;
  min-width: 90vw;
  padding: 15px 0;
  border: none;
  background-color: $main-color;
  color: $bg-color;
  font-family: inherit;
  font-size: 24px;
  font-weight: 800;
  line-height: 1.1;
  letter-spacing: 2.5px;
  text-align: center;

  &::after {
    position: absolute;
    top: 4px;
    left: 4px;
    z-index: -1;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 2px;
    background-color: $text-color;
    opacity: 0.3;
    filter: blur(3px);
    transition: top 0.2s linear, left 0.2s linear, width 0.2s linear, height 0.2s linear, background-color 0.2s linear, opacity 0.2s linear;
  }

  &:hover,
  &:focus {
    &::after {
      top: -2px;
      left: -2px;
      width: calc(100% + 4px);
      height: calc(100% + 4px);
      background-color: $main-color;
      opacity: 0.8;
      filter: blur(5px);
    }
  }

  &:active {
    &::after {
      opacity: 0;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 24px;
    font-size: 26px;
  }

  &--confirm {
    background-color: $confirm-color;
    background-image: radial-gradient(circle at 65% 15%, $confirm-color, darken($confirm-color, 0.9));

    &:hover,
    &:focus {
      &::after {
        top: -5px;
        left: -5px;
        width: calc(100% + 10px);
        height: calc(100% + 10px);
        background-color: $confirm-color;
      }
    }
  }
}
